.btn {
  color: #fff;
  text-transform: capitalize;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 20px 30px;
  font-size: 30px;
  transition: all .3s;
  position: absolute;
  top: 30px;
  left: 50%;
}

.win {
  background-color: green;
}

.fail {
  background-color: red;
}

.danger {
  background-color: #8a2be2;
}

.atention {
  background-color: #adff2f;
}

